.App {
  text-align: center;
  background-color: rgb(239, 253, 217);
  /* cursor: url('icons8-cursor-50.cur'), default; */
}



/* Dark mode styles */
.App.dark-mode {
  background: linear-gradient(135deg, #2c3e50 0%, rgb(32, 49, 4) 100%);
  color: white;
}


.App.dark-mode .nav-button {
  -webkit-text-fill-color: white;
  color: white; /* Change the text color to white */
}

.App.dark-mode .nav-button:hover {
  -webkit-text-fill-color: #65c269;
}


.App.dark-mode .nav-button::after {
  content: '➔'; /* Unicode arrow character */
  position: absolute;
  right: -20px; /* Start off to the right */
  top: 50%;
  transform: translateY(-50%);
  opacity: 1; /* Always visible for testing */
  color: #65c269; /* Ensure the arrow is visible */
}

.App.dark-mode .nav-button:hover::after {
  right: 10px; /* Move arrow into view */
}





@media (prefers-reduced-motion: no-preference) {
  .App-logo {
