.floating-text {
  animation: float-text 2s ease-in-out forwards; /* Animation name, duration, timing function, and direction */
}

@keyframes float-text { /* Define the animation keyframes */
  from { transform: translateY(50px); opacity: 0; } /* Start from a higher position and transparent */
  to { transform: translateY(0px); opacity: 1; } /* End at original position and fully opaque */
}

