.nav-button {
  position: relative;
  background-color: transparent;
  width: 200px;
  height: 100px;
  border: transparent;
  font-size: 20px;
  overflow: hidden;
  background: linear-gradient(to right, #6fbb73, #64f56b 50%, #6fbb73 50%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: black;
  background-size: 200% 100%;
  background-position: 100%;

  transition: background-position 500ms cubic-bezier(0.25, 1, 0.5, 1), transform 500ms cubic-bezier(0.25, 1, 0.5, 1);
}
.nav-button:hover {
  background-position: 0 100%;
  transform: translateX(20px);
  -webkit-text-fill-color: #65c269;
}

.nav-button::after {
  content: '➔'; /* Unicode arrow character */
  position: absolute;
  right: -20px; /* Start off to the right */
  top: 50%;
  transform: translateY(-50%);
  opacity: 1; /* Always visible for testing */
  color: black; /* Ensure the arrow is visible */
}

.nav-button:hover::after {
  right: 10px; /* Move arrow into view */
}


