.p-button .fa-arrow {
  position: absolute; /* Position the icon relative to the button */
  top: 20%; /* Position the icon vertically centered */
  right: 0; /* Position the icon on the right side of the button */
  padding-right: 10px;
  transition: transform 0.3s ease; /* Apply transition effect to transform property */
}

.p-button:hover .fa-arrow {
  transform: translate(20%, -20%); /* Move the icon up and to the right */
}

.fa-arrow {
